import React, { useEffect, useState, useRef } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "../../../components/Grid/GridItem.js";
import GridContainer from "../../../components/Grid/GridContainer.js";
import 'date-fns';
import CustomInput from "../../../components/CustomInput/CustomInput.js";
import Button from "../../../components/CustomButtons/Button.js";
import Card from "../../../components/Card/Card.js";
import CardHeader from "../../../components/Card/CardHeader.js";
import CardBody from "../../../components/Card/CardBody.js";
import CardFooter from "../../../components/Card/CardFooter.js";
import Snackbar from '@material-ui/core/Snackbar';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { addRequirement } from '../reducer'
import MuiAlert from '@material-ui/lab/Alert';
import List from './List'
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Model from '../../../utils/Model'
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputLabel from '@material-ui/core/InputLabel';
import moment from 'moment'
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import readXlsxFile from 'read-excel-file'
import FormControl from '@material-ui/core/FormControl';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
const styles = (theme) => ({
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
})

const useStyles = makeStyles(styles);
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
function RequirementAdd(props) {
  const classes = useStyles();
  const { history, authen, requirement, addRequirement } = props
  const requirementOptions = authen.userInfo.bp === 'Mua Hang' ? [
    'YCM',
  ] : authen.userInfo.bp === 'Kho' ? [
    'PNK',
    'PXK',
  ] :  [
    'YVT',
  ] 
  const { addRequirementSuccess } = requirement
  const [requirementName, setRequirementName] = useState('')
  const [password, setPassword] = useState('')
  const [department, setDepartment] = useState('')
  const [open, setOpen] = React.useState(false);
  const [needTime, setNeedTime] = useState(0)
  const { header } = authen
  const [requirementList, setRequirementList] = useState([])
  const [isAddNew, setIsAddNew] = useState(true)
  const [currentProductIndex, setCurrentProductIndex] = useState(-1)
  const [openModal, setOpenModal] = useState(false)
  const [currentProductName, setCurrentProductName] = useState('')
  const [currentProductCode, setCurrentProductCode] = useState('')
  const [currentTotal, setCurrentTotal] = useState(0)
  const [currentDistributor, setCurrentDistributor] = useState('')
  const [currentInformation, setCurrentInformation] = useState('')
  const [currentUnit, setCurrentUnit] = useState('')
  const [currentManufacture, setCurrentManufacture] = useState('')
  const [requirementType, setRequirementType] = useState(requirementOptions[0])
  const [project, setProject] = useState('')
  const dateFormat = "DD-MM-YYYY"
  const [selectedDate, setSelectedDate] = useState(moment().format(dateFormat))
  const inputFile = useRef(null) 
  console.log('selectedDate', selectedDate)
  const handleDateChange = date => {
    console.log('date', date)
    // setSelectedDate(moment(date).format(dateFormat));
    setSelectedDate(moment(date).format(dateFormat));
  };
  function resetState() {
    setCurrentProductName('')
    setCurrentProductCode('')
    setCurrentTotal(0)
    setCurrentDistributor('')
    setCurrentInformation('')
    setCurrentUnit('')
    setCurrentManufacture('')
  }
  useEffect(() => {
    if (currentProductIndex !== -1) {
      const requiermentTemp = requirementList[currentProductIndex]
      setCurrentProductName(requiermentTemp.tvt)
      setCurrentProductCode(requiermentTemp.mvt)
      setCurrentTotal(requiermentTemp.sl)
      setCurrentDistributor(requiermentTemp.ncc)
      setCurrentInformation(requiermentTemp.ts)
      setCurrentUnit(requiermentTemp.dv)
      setCurrentManufacture(requiermentTemp.hsx)
    }
  }, [currentProductIndex])

  useEffect(() => {
    if (addRequirementSuccess && addRequirementSuccess.success !== null) {
      console.log('================', moment(selectedDate).format('YYYY-MM-DD'))
      setOpen(true);
      // if(addRequirementSuccess.success === true && addRequirementSuccess.listProduct && addRequirementSuccess.listProduct.length > 0){
      //   const params = Object.assign({}, Model, {
      //     "myc": 'YCM' + requirementName,
      //     "nyc": moment(selectedDate).format('YYYY-MM-DD'),
      //     "tuseryc": authen.userInfo.name,
      //     "bpyc": department,
      //     "dayc": project,
      //     "lvtyc": `${JSON.stringify(addRequirementSuccess.listProduct)}`,
      //     "statusyc": "Ch duyt",
      //     "iduseryc": authen.userInfo.id,
      //     "refmyc": requirementType + requirementName
      //   })
      //   addRequirement(header, params)
      // }
    }
  }, [addRequirementSuccess])
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
    history.goBack()
  };
  const handleCloseModal = (event, reason) => {
    resetState()
    setOpenModal(false);
  };


  function deleteSelectedIndex(index) {
    console.log('deleteSelectedIndex', index)
    console.log('deleteSelectedIndex', requirementList)
    console.log('deleteSelectedIndex', requirementList.slice(index))
    setRequirementList(requirementList.length === 1 ? [] : requirementList.filter((e, i) => i !== index))
  }
  function renderModal() {
    return (
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={openModal}
        onClose={handleCloseModal}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <GridContainer 
        justify="center"
        alignItems="center">

          <GridItem xs={12} sm={12} md={12}>
            <Card>
              <CardHeader color="primary">
                <h4 className={classes.cardTitleWhite}>Thm mi vt t</h4>
                <p className={classes.cardCategoryWhite}>Hon thnh thng tin vt t</p>
              </CardHeader>
              <CardBody>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="Tn vt t"
                      id="productname"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        console.log('Productname', event.target.value)
                        setCurrentProductName(event.target.value)
                      }}
                      value={currentProductName}
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="M vt t"
                      id="productCode"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        setCurrentProductCode(event.target.value)
                        console.log('Email address', event.target.value)
                      }}
                      value={currentProductCode}
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="Nh cung cp"
                      id="distributor"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        console.log('Productname', event.target.value)
                        setCurrentDistributor(event.target.value)
                      }}
                      value={currentDistributor}
                    />
                  </GridItem>
                </GridContainer>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="Hng sn xut"
                      id="manufacture"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        console.log('Productname', event.target.value)
                        setCurrentManufacture(event.target.value)
                      }}
                      value={currentManufacture}
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="n v"
                      id="unit"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        setCurrentUnit(event.target.value)
                        console.log('Email address', event.target.value)
                      }}
                      value={currentUnit}
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText="S lng"
                      id="total"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        setCurrentTotal(event.target.value)
                        console.log('Email address', event.target.value)
                      }}
                      value={currentTotal}
                      error={isNaN(currentTotal)} 
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={12} md={12}>
                    <CustomInput
                      labelText="Thng s"
                      id="information"
                      formControlProps={{
                        fullWidth: true
                      }}
                      onChange={(event) => {
                        setCurrentInformation(event.target.value)
                        console.log('Email address', event.target.value)
                      }}
                      value={currentInformation}
                    />
                  </GridItem>

                </GridContainer>

              </CardBody>
              <CardFooter>
                <Button color="primary" onClick={() => {
                  const newProduct = {

                    tvt: currentProductName,
                    mvt: currentProductCode,
                    ts: currentInformation,
                    hsx: currentManufacture,
                    dv: currentUnit,
                    sl: Number(currentTotal),
                    ncc: currentDistributor

                  }
                  if (isAddNew) {
                    setRequirementList(requirementList.concat(newProduct))
                  } else {
                    let temp = requirementList.slice()
                    temp[currentProductIndex] = newProduct
                    setRequirementList(temp)
                  }
                  handleCloseModal()
                }}
                  disabled={!currentProductName || !currentProductCode || !currentTotal || isNaN(currentTotal)}
                >{'Lu'}</Button>
              </CardFooter>
            </Card>
          </GridItem>
        </GridContainer>
      </Modal>
    )
  }
  return (
    <div>
      <Snackbar open={open} autoHideDuration={1000} onClose={handleClose}>
        <Alert onClose={handleClose} severity={addRequirementSuccess && addRequirementSuccess.success === true ? "success" : "error"}>
          {addRequirementSuccess && addRequirementSuccess.success === true ? `Thm yu cu thnh cng!` : `Thm yu cu tht bi!`}
        </Alert>
      </Snackbar>
      <GridContainer justify="center"
        alignItems="center">

        <GridItem xs={12} sm={12} md={8}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Thm mi yu cu</h4>
              <p className={classes.cardCategoryWhite}>Hon thnh thng tin yu cu</p>
            </CardHeader>
            <CardBody>
              {/* <GridContainer>
                
              </GridContainer> */}
              <GridContainer>
              
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="S YC"
                    id="requirementname"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={(event) => {
                      console.log('Requirementname', event.target.value)
                      setRequirementName(event.target.value)
                    }}
                    value={requirementName}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="B phn"
                    id="department"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={(event) => {
                      // setDepartment(event.target.value)
                      console.log('Email address', event.target.value)
                    }}
                    value={authen.userInfo.bp}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    labelText="D n"
                    id="project"
                    formControlProps={{
                      fullWidth: true
                    }}
                    onChange={(event) => {
                      console.log('project', event.target.value)
                      setProject(event.target.value)
                    }}
                    value={project}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                {/* <GridItem xs={12} sm={12} md={4}>
                  
                </GridItem> */}
                <GridItem xs={12} sm={12} md={12}>
                  <div style={{display:'flex', flex: 1, flexDirection: 'row', justifyContent: 'space-between', alignItems : 'center'}}>
                  <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="uncontrolled-native">Loi YC</InputLabel>
                  <NativeSelect
                    defaultValue={requirementOptions[0]}
                    // value={requirementType}
                    onChange={(event) => {
                      setRequirementType(event.target.value)
                      console.log('hihi', event.target.value)
                    }}
                    inputProps={{
                      name: 'requirementType',
                      id: 'uncontrolled-native',
                    }}
                  >
                    {requirementOptions.map((option, index) => (
                      <option key={index} value={option}>
                        {option}
                      </option>
                    ))}
                  </NativeSelect>
                  </FormControl>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <KeyboardDatePicker
                        format="dd-MM-yyyy"
                        margin="normal"
                        id="date-picker-dialog"
                        label="Ngy cn"
                        value={moment(selectedDate).format(dateFormat)}
                        onChange={handleDateChange}
                        KeyboardButtonProps={{
                          'aria-label': 'change date',
                        }}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                  </div>
                  
                </GridItem>
              </GridContainer>
            </CardBody>
            <div style={{display :'flex', flexDirection: 'row', flex: 1, justifyContent: 'space-between'}}>
            <CardFooter>
              <Button color="primary" onClick={() => {
                setIsAddNew(true)
                setOpenModal(true)
                // setRequirementList(requirementList.slice().push(newProduct))
              }}>{'Thm vt t'}</Button>
            </CardFooter>
            <CardFooter>
              <Button color="primary" onClick={() => {
                  // `current` points to the mounted file input element
                inputFile.current.click();
                // setRequirementList(requirementList.slice().push(newProduct))
              }}>{'Thm vt t t file'}</Button>
            </CardFooter>
            </div>
            
          </Card>
        </GridItem>
      </GridContainer>
      <List
        requirementList={requirementList.length > 0 ? requirementList.map((e, i) => {
          let tempArr = [i + 1]
          Object.keys(e).forEach((key, index) => {
            if (index < 8)
              tempArr.push(e[key])
          })
          return tempArr
        }) : []}
        tableHead={["TT", "M vt t", , "Tn vt t", "Thng s", "Hng sn xut", "n v", "S lng", "Nh cung cp"]}

        // tableHead={["TT", "S ti liu", "Thi gian", "Ngi YC", 'B phn', 'D n', 'Mc u tin']}
        setIsAddNew={setIsAddNew}
        setRequirementList={setRequirementList}
        setCurrentProductIndex={setCurrentProductIndex}
        setOpenModal={setOpenModal}
        deleteSelectedIndex={deleteSelectedIndex}
      ></List>
      <Button color="primary" onClick={() => {
        console.log('================', moment(selectedDate, dateFormat).format('YYYY-MM-DD'))
        const params = Object.assign({}, Model, {
          "myc": requirementType + requirementName,
          "nyc": moment(selectedDate, dateFormat).format('YYYY-MM-DD'),
          "tuseryc": authen.userInfo.name,
          "bpyc": authen.userInfo.bp,
          "dayc": project ? project : '999999',
          "lvtyc": `${JSON.stringify(requirementList)}`,
          "statusyc": "Ch duyt",
          "iduseryc": authen.userInfo.id,
        })
        addRequirement(header, params)
      }}
      disabled={!requirementName || requirementList.length === 0}
      >Lu</Button>
      {/* <Button color="primary" onClick={() => {
        // history.push('/admin/pdf')
      }}>Xut Excel</Button> */}
      <input type='file' id='file' ref={inputFile} style={{display: 'none'}} onChange={(evnet)=>{
        console.log('evnet', evnet.target.files[0])
        readXlsxFile(evnet.target.files[0]).then((rows) => {
          console.log('rows', rows)
          const listProduct = rows.filter((e, i) => i > 0).map(e => {
            return {
            mvt: e[0],
            tvt: e[1],
            ts: e[6],
            hsx: e[3],
            dv: e[4],
            sl: Number(e[5]),
            ncc: e[2]

          }
          })
          setRequirementList(requirementList.concat(listProduct))
        // `rows` is an array of rows
        // each row being an array of cells.
        })
      }}/>
      {renderModal()}
    </div>
  );
}


const mapStateToProps = state => ({
  authen: state.authen,
  requirement: state.requirement
})

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      addRequirement,
    },
    dispatch
  )
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RequirementAdd)
